$dark: #000;
$white: #fff;
$primary: #cd730b;
$secondary: #eee;

html {
  font-family: sans-serif;
  font-size: 16px;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: $dark;
}

header {
  background: #eee;
}

img {
  width: 100%;
  height: auto;
}

/* Header */
.header {
  .navbar {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 1rem;
    padding-bottom: 1rem;

    &-menu {
      display: flex;
      flex-wrap: nowrap;
      flex-flow: row wrap;
      list-style: none;

      .nav-item {
        &:not(:last-child) {
          margin-right: 1rem;
        }
      }

      .nav-link {
        font-size: 18px;
        color: $dark;

        &.active {
          color: $primary;
          font-weight: bold;
        }
      }
    }
  }
}

// Home page hero
.hero {
  max-width: 992px;
  margin-top: 2rem;
  margin-left: auto;
  margin-right: auto;
  border-radius: 0.5rem;
  overflow: hidden;
  box-shadow: 0 0 0 3px darken($primary, 5%);
  background-color: darken($primary, 5%);

  &-body {
    position: relative;

    .hero-image {
      @media (min-width: 992px) {
        height: 60vh;
        object-fit: cover;
      }
    }

    .hero-btn {
      padding: 1rem 2rem;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background: $primary;
      box-shadow: 0 0 0 3px $white;
      border-radius: 4px;
      font-size: 17px;
      font-weight: bold;
      transition: 0.3s;

      &:hover {
        box-shadow: 0 0 0 3px $primary, 0 0 0 6px $white;
        opacity: 95%;
      }
    }
  }

  &-footer {
    background: $secondary;
    padding: 2rem 1rem;

    .hero-tagline {
      font-size: 28px;
      font-weight: lighter;
    }
  }
}

// Main content
#content {
  // margin: 2rem auto;
}

/* Utilitites */
.container {
  padding-left: 1rem;
  padding-right: 1rem;
  max-width: 1140px;
  margin-left: auto;
  margin-right: auto;
}
